<template>
    <div class="car-desc">
        <section class="container">
            <div class="row">
                <div class="col-xs-12">
                    <v-breadnav>{{`${leaseCarDesc.carName} ${leaseCarDesc.carSeriesName}`}}</v-breadnav>
                </div>
            </div>       
            <div class="row">
                <div class="col-xs-12">
                    <section class="desc-header clearfix">
                        <div class="col-xs-5">
                            <v-slider :slideArr = "getSliderArr"></v-slider>
                        </div>
                        <div class="col-xs-7">
                            <h3>{{`${leaseCarDesc.carName} ${leaseCarDesc.carSeriesName}`}}</h3>
                            <p class="factory-price">{{`厂商指导价：${leaseCarDesc.firmPrice}`}}</p>
                            <p class="prefer-info"><span class="tag">免首付</span>{{`0首付超值精选限量抢，详情咨询商家：${leaseCarDesc.companyMobile}`}}</p>
                            <h4 class="title-info">购车方案</h4>
                            <div class="plans clearfix">
                                 <div class="col-xs-6"><a class="buycar-plan active " @click="PlanOneClick($event,leaseCarDesc.firstPayment,leaseCarDesc.monthPayment,leaseCarDesc.periods)"><span>分期支付</span>{{`${leaseCarDesc.monthPayment}x36期`}}</a></div>
                                 <div class="col-xs-6"><a class="buycar-plan"  @click="PlanOneClick($event,leaseCarDesc.firstPayment1,leaseCarDesc.monthPayment1,leaseCarDesc.periods1)"><span>分期支付</span>{{`${leaseCarDesc.monthPayment1}x48期`}}</a></div>
                                 <div class="col-xs-12"><a class="buycar-plan" @click="PlanOneClick($event,leaseCarDesc.tfirstYearFirstPay,leaseCarDesc.tfirstYearMonthPayment,leaseCarDesc.tperiods)"><span>先开一年</span>{{`一年后一次性支付尾款${leaseCarDesc.finalPayment}(或分期${leaseCarDesc.tfirstYearMonthPayment}x36期`}}</a></div>
                            </div>
                            <ul class="price-desc clearfix">
                                <li class="first-pay">                                   
                                    <p>首付</p>
                                    <p><span class="price-num">{{firstPay}}</span></p>
                                </li>
                                <li class="month-pay">                                  
                                    <p>月供</p>
                                    <p><span class="price-num">{{monthPay}}</span></p>
                                </li>
                                <li class="time-period">                                    
                                    <p>期数</p>
                                    <p><span class="price-num">{{`${periods}期`}}</span></p>
                                </li>
                            </ul>
                            <div class="next-year">
                                <div class="labels" v-if="Object.keys(leaseCarDesc).length != 0 ">
                                    <span class="label-item" v-for="(item,index) in labels(leaseCarDesc.labels)" :key="index">{{item}}</span>
                                </div>
                                <p>{{`购车时支付${leaseCarDesc.buyPay}(购车后返还)`}}</p>
                                <p>{{`需向门店支付交车服务费${leaseCarDesc.serviceFee}`}}</p>
                                <button class="btn pull-right">预约到店</button>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
            <div class="row">
                <div class="col-xs-12">
                    <section class="car-detail " v-if="leaseCarDesc" data-target="#fixed-navbar" data-offset="0">
                        <nav>
                            <a href="#base-config" class="base-config active">基本配置</a>
                            <a href="#need-know" class="need-know">购车须知</a>
                            <a href="#buy-explain" class="buy-explain">购车说明</a>
                            <a href="#car-details" class="car-desc">车辆信息</a>
                        </nav>
                        <article>
                            <div class="title" id="base-config">
                                <h3>基本配置</h3>
                                <a class="more pull-right" data-toggle="modal" data-target="#config-modal">查看全部</a>
                            </div>
                            <ul class="configs clearfix">
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>车身结构</label>
                                        <span>{{leaseCarDesc.cheshenjiegou}}</span>
                                    </div>                                
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>长/宽/高</label>
                                        <span>{{`${leaseCarDesc.length}/${leaseCarDesc.width}/${leaseCarDesc.height}mm`}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>发动机</label>
                                        <span>{{leaseCarDesc.fadongjixinghao}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>变速箱</label>
                                        <span>{{leaseCarDesc.biansuxiangtype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>驱动方式</label>
                                        <span>{{leaseCarDesc.qudongtype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>燃料形式</label>
                                        <span>{{leaseCarDesc.ranliaotype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>综合油耗</label>
                                        <span>{{leaseCarDesc.cheshenjiegou}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>车辆规格</label>
                                        <span>{{leaseCarDesc.classType}}</span>
                                    </div>
                                </li>
                            </ul>
                            <div class="title" id="need-know">
                                <h3>购车须知</h3>
                            </div>
                            <div class="question-img">
                                <img :src="leaseCarDesc.buyKnowUrl" alt="" class="img-responsive">
                            </div>
                             <div class="title" id="buy-explain">
                                <h3>购车说明</h3>
                            </div>
                            <div class="question-img">
                                <img :src="leaseCarDesc.buyMarkUrl" alt="" class="img-responsive">
                            </div>
                            <div class="title" id="car-details">
                                <h3>车辆信息</h3>
                            </div>
                            <div class="car-detail-iframe" v-html="leaseCarDesc.descUrl">
                                
                            </div>
                        </article>
                    </section>
                </div>
            </div>
        </section>
        <section class="fixed-nav" v-show="scrollHeight>=620">
            <nav class="navbar navbar-default navbar-fixed-top">
                <div class="container">
                    <ul class="nav navbar-nav" id="fixed-navbar">
                        <li><a href="#base-config" class="active" >基础配置</a></li>
                        <li><a href="#need-know">购车须知</a></li>
                        <li><a href="#buy-explain">购车说明</a></li>
                        <li><a href="#car-details">车辆信息</a></li>
                    </ul>
                    <button class="btn btn-default pull-right">预约到店</button>
                </div>
            </nav>
        </section>
        <section class="config-modal">
            <v-config :config ="configArr"></v-config>
        </section>
    </div>       
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vSlider from "@/components/pc/slider"
import vConfig from "@/components/pc/carConfigModal"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            carId:this.$route.params.id,
            leaseCarDesc:{},
            scrollHeight:0,
            configArr:{},
            firstPay:0,
            monthPay:0,
            periods:0
        }
    },
    components:{
        vBreadnav,
        vSlider,
        vConfig 
    },
    methods:{
        getDescData:function(){
            let data = {};
            let _this = this;
            data.cartId = this.carId;
            data.platForm = "020003";
            $.ajax({
                url:PC_COMMONURL+'/queryLeaseCarDetail',
                type:"get",
                data:data,
                dataType:"json",
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.leaseCarDesc = data.data.carDetail;
                        _this.firstPay = data.data.carDetail.firstPayment;
                        _this.monthPay = data.data.carDetail.monthPayment;
                        _this.periods = data.data.carDetail.periods;
                        _this.configArr = Object.assign(data.data.params1,data.data.params2);
                    }
                }
            })
        },
        PlanOneClick:function(event,firstpay,monthPay,period){
            $(".buycar-plan").removeClass("active");
            $(event.target).addClass("active");
            this.firstPay = firstpay;
            this.monthPay = monthPay;
            this.periods = period;
        },
        labels:function(labels){
            if(labels.indexOf(',')!= -1){
                console.log(labels.split(","));
                 return labels.split(",");
            }else{
                return [labels]
            }
           
        }
    },
    computed:{
        getSliderArr:function(){          
            let newSliderArr = [];
            if(Object.keys(this.leaseCarDesc).length != 0){
                let slideArr = this.leaseCarDesc.icons;
                slideArr.forEach(function(n){
                    let obj = new Object();
                    obj.imgUrl = n;
                    newSliderArr.push(obj);
                })               
            }
            return newSliderArr;
        }
    },
    mounted:function(){
        this.getDescData();
         let _this = this;
        $(document).scroll(function(){
            let scrollHeight = $(document).scrollTop();
            _this.scrollHeight = scrollHeight;
            if($("#base-config").offset() && $("#need-know").offset() && $("#buy-explain").offset() && $("#car-details").offset()){
                let baseHeight = $("#base-config").offset().top;
                let needKnowHeight =$("#need-know").offset().top;
                let buyExplaninHeight = $("#buy-explain").offset().top;
                let carDetailsHeight = $("#car-details").offset().top;
                if(baseHeight-30 <= scrollHeight && scrollHeight < needKnowHeight ){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(0).find('a').addClass("active");
                }else if(needKnowHeight-30  <= scrollHeight && scrollHeight < buyExplaninHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(1).find('a').addClass("active");
                }else if(buyExplaninHeight-30 <= scrollHeight && scrollHeight < carDetailsHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(2).find('a').addClass("active");
                }else if(carDetailsHeight-30 <= scrollHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(3).find('a').addClass("active");
                }
            }
            
        })  
    }
    
}
</script>



